.product-variant-picker__list .product-variant-picker__container {
  flex-direction: column;
  gap: 1.5rem;
  display: flex;
}

.product-variant-picker__list .product-variant-picker__option__radiobutton {
  border: 1px solid var(--gsc-text-color-35);
  border-radius: 100%;
  align-self: center;
  width: 20px;
  height: 20px;
}

.product-variant-picker__list label.product-variant-picker__option {
  border: 1px solid var(--gsc-text-color-35);
  border-radius: 5px;
  flex-direction: column;
  gap: .75rem;
  padding: 1rem;
  display: flex;
}

.product-variant-picker__list label.product-variant-picker__option.selected {
  color: inherit;
  border: 1px solid rgb(var(--gsc-accent-color));
  background: #696cff14;
}

.product-variant-picker__list label.product-variant-picker__option.selected .product-variant-picker__option__radiobutton {
  border-color: rgb(var(--gsc-accent-color));
}

.product-variant-picker__list label.product-variant-picker__option.selected .product-variant-picker__option__radiobutton:after {
  background: rgb(var(--gsc-accent-color));
  content: "";
  border-radius: 100%;
  width: 14px;
  height: 14px;
  margin-top: 2px;
  margin-left: 2px;
  display: block;
}

.product-variant-picker__list .product-variant-picker__option__most-popolar-label {
  background: rgb(var(--gsc-accent-color));
  color: var(--gsc-button-text-color-100);
  border-radius: 2px;
  align-self: flex-start;
  margin-left: 3.7rem;
  padding: 1px 5px;
  font-size: 1rem;
}

.product-variant-picker__list .product-variant-picker__option__content {
  gap: 1.6rem;
  display: flex;
}

.product-variant-picker__list .product-variant-picker__option__details {
  flex-direction: column;
  flex: 1;
  display: flex;
}

.product-variant-picker__list .product-variant-picker__option__title {
  color: var(--gsc-text-color-100);
  font-size: 1.6rem;
  font-weight: 600;
}

.product-variant-picker__list .product-variant-picker__option__description {
  color: var(--gsc-text-color-85);
  font-size: 1.2rem;
}

.product-variant-picker__list .product-variant-picker__option__price {
  color: var(--gsc-text-color-100);
  align-self: center;
  font-size: 1.8rem;
  font-weight: 500;
}

.product-price .product-price__content {
  align-items: baseline;
  gap: 7px;
}

.product-price__shipping-info {
  font-size: 12px;
  position: relative;
  bottom: 3px;
}

.product-shipping-tag {
  padding: 2rem 2.4rem;
}

.product-shipping-tag__icon-container {
  position: relative;
}

.product-shipping-tag__icon-flag {
  border-radius: 4px 4px 0;
  position: absolute;
  top: 2px;
  left: -2px;
}

