.product-price {
    .product-price__content {
    align-items: baseline;
    gap: 7px;
  }

  &__shipping-info {
    font-size: 12px;
    position: relative;
    bottom: 3px;
  }
}

.product-shipping-tag {
  padding: 2rem 2.4rem;

  &__icon-container {
    position: relative;
  }

  &__icon-flag {
    position: absolute;
    top: 2px;
    left: -2px;
    border-radius: 4px 4px 0 4px;
  }
}