.product-variant-picker__list {
  .product-variant-picker__container {
    display: flex;
    gap: 1.5rem;
    flex-direction: column;
  }

  .product-variant-picker__option__radiobutton {
    width: 20px;
    height: 20px;
    border-radius: 100%;
    border: 1px solid var(--gsc-text-color-35);
    align-self: center;
  }

  label.product-variant-picker__option {
    gap: .75rem;
    display: flex;
    display: flex;
    flex-direction: column;
    padding: 1rem;
    border-radius: 5px;
    border: 1px solid var(--gsc-text-color-35);

    &.selected {
      background: rgba(105, 108, 255, 0.08);
      color: inherit;
      border: 1px solid rgb(var(--gsc-accent-color));
      
      .product-variant-picker__option__radiobutton {
        border-color: rgb(var(--gsc-accent-color));

        &::after {
          width: 14px;
          height: 14px;
          margin-left: 2px;
          margin-top: 2px;
          background: rgb(var(--gsc-accent-color));
          border-radius: 100%;
          content: '';
          display: block;
        }
      }
    }
  }

  .product-variant-picker__option__most-popolar-label {
    background: rgb(var(--gsc-accent-color));
    border-radius: 2px;
    font-size: 1rem;
    color: var(--gsc-button-text-color-100);
    padding: 1px 5px;
    align-self: flex-start;
    margin-left: 3.7rem;
  }

  .product-variant-picker__option__content {
    display: flex;
    gap: 1.6rem;
  }

  .product-variant-picker__option__details {
    display: flex;
    flex: 1;
    flex-direction: column;
  }

  .product-variant-picker__option__title {
    font-size: 1.6rem;
    font-weight: 600;
    color: var(--gsc-text-color-100);
  }

  .product-variant-picker__option__description {
    font-size: 1.2rem;
    color: var(--gsc-text-color-85);
  }

  .product-variant-picker__option__price {
    align-self: center;
    font-size: 1.8rem;
    color: var(--gsc-text-color-100);
    font-weight: 500;
  }
}